.TopMenu{
    clear: both;
    width: 100%;
    height: 100px;
    line-height: 100px;
    background: #1e88e5;
    color: #fff;
    ul{
        width: 100%;
        height: 100px;
        list-style: none;
        li{
            height: auto;
        }
        li.Menu{
            width: calc(100%/3);
            list-style: none;
            float: left;
            font-size: 28px;
            text-align: center;
        }
        li.animate{
            animation-name: titleBackground;
            animation-duration: 2s;
            animation-iteration-count: infinite;
        }
    }
}

a{
    text-decoration: none;
    color: #fff;
}

@keyframes titleBackground {
    0% {
        background: #6ab7ff;
        color: #000;
    }
    25% {
        background: #1e88e5;
        color: #fff;
    }
    50% {
        background: #6ab7ff;
        color: #000;
    }
    75%{
        background: #005cb2;
        color: #fff;
    }
    100%{
        background: #6ab7ff;
        color: #000;
    }
}