.Careers{
    clear: both;
    width: 100%;
    height: 500px;
    background: #fff;
    .title{
        width: 100%;
        padding-top: 50px;
        h3{                        
            text-align: center;
            font-size: 38px;
            line-height: 48px;
            display: block;
        }
        .logo{
            font-size: 40px;
        }
        .miniTitle{
            color: #1266FF;
        }
    }
    .career{
        clear: both;
        margin-top: 50px;
        display: flex;
        justify-content: space-evenly;
        .career_area{
            width: 30%;
            .group_name{
                font-size: 18px;
                height: 48px;
            }
            ul{
                li{
                    
                    ul{
                        li{
                            padding-left: 20px;
                        }
                        
                    }
                }
                
            }
        }
    }
}

ul, li{list-style: none;height: 32px;}