.AboutMe{
    clear: both;
    width: 100%;
    background: #F0EEE2;
    height: 400px;
    .title{
        width: 100%;
        padding-top: 50px;
        h3{                        
            text-align: center;
            font-size: 38px;
            line-height: 48px;
            display: block;
        }
        .logo{
            font-size: 40px;
        }
    }
    .contents{
        width: 100%;
        font-size: 18px;
        .list{
            width: calc(50% - 80px);
            text-align: center;
            float: left;
            margin: 10px;
            padding: 10px;
            .logo{
                font-size: 28px;
            }
            p{
                font-weight: 700;
            }
        }  
    }
    
}

.logo{
    font-size: 18px;
    width: 10px;
    margin: 0;
    padding: 0;
} 