.Skills{
    clear: both;
    width: 100%;
    height: 700px;
    background: #F0EEE2;
    .title{
        width: 100%;
        padding-top: 50px;
        h3{                        
            text-align: center;
            font-size: 38px;
            line-height: 48px;
            display: block;
        }
        .logo{
            font-size: 40px;
        }
    }
    .category{
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        .item {
            width: calc(100%/3 - 4px);
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-evenly;
            margin-top: 40px;
            border-right: 4px solid #4C4C4C;
            .category_title{
                width: 100%;
                font-size: 28px;
                font-weight: 700;
                text-align: center;
                margin: 25px;
            }
            .iconBox{
                width: 120px;
                margin: 15px 30px;
                img{
                    width: 100%;
                }
            }
        }
        .item:last-child{
            border-right: 0;
        }
    }
    
}